// @flow

import { assert } from 'chai'
import { describe, it } from 'mocha'

import * as Extended from '../../src/hd/extended.js'
import { HARDENED } from '../../src/hd/common.js'
import * as Public from '../../src/hd/public.js'
import privateFixures from './fixtures/private.json'
import publicFixures from './fixtures/public.json'

let { network } = privateFixures

describe('Testing HD Private Key', function () {
  privateFixures.string.forEach(test => {
    const base58Key = test[0]
    const privateKey = {
      privateKey: test[1],
      version: test[2],
      depth: test[3],
      parentFingerPrint: test[4],
      childNumber: test[5],
      chainCode: test[6]
    }

    it(`Extended key from string: ${test[0]}`, function () {
      const resultedXkey = Extended.fromString(base58Key, network)
      assert.deepEqual(resultedXkey, privateKey)
    })

    it(`Extended key to string: ${test[1]}`, function () {
      const resultedBase58Key = Extended.toString(privateKey, network)
      assert.equal(resultedBase58Key, base58Key)
    })
  })

  privateFixures.fromIndex.forEach(test => {
    it(`Deriving key ${test[0]} for index ${test[2]}`, async function () {
      const privateKey = test[0]
      const chainCode = test[1]
      const index = test[2]
      const publicKey = test[6]
      const expectedChildKey = {
        privateKey: test[3],
        chainCode: test[4],
        childNumber: test[5]
      }
      // $FlowFixMe
      const xKey = Extended.fromExtended({ privateKey, chainCode, publicKey }, network)
      // $FlowFixMe
      const childKey = await Extended.fromIndex(xKey, index)
      assert.deepEqual({
        privateKey: childKey.privateKey,
        chainCode: childKey.chainCode,
        childNumber: childKey.childNumber
      }, expectedChildKey)
    })
  })

  privateFixures.fromPath.forEach(test => {
    it(`Deriving HD key ${test[0]} from HD Settings ${
      test[3]
    }`, async function () {
      const xkey = test[0]
      const parentKey = Extended.fromString(xkey, network)

      let account = 0
      let coinType = 0
      let bips = test[2]
      if (!bips) bips = [84]
      if (test[1]) {
        account = test[1][0]
        coinType = test[1][1]
      }
      let testIndex = 3

      const testPath = async (path) => {
        const hdKey = await Extended.fromPath(parentKey, path)
        const xprivkey = Extended.toString(hdKey, network)
        const pubKey = await Extended.toPublic(hdKey, network)
        const xpubkey = Public.toString(pubKey, network)
        assert.equal(xprivkey, test[testIndex++], 'xpriv')
        assert.equal(xpubkey, test[testIndex++], 'xpub')
      }

      for (const purpose of bips) {
        if (purpose === 32) {
          await testPath(['m', `${account}`, 0].join('/'))
          testIndex -= 2
          await testPath([account, 0])
        } else {
          await testPath(['m', `${purpose}'`, `${coinType}'`, `${account}'`, 0].join('/'))
          await testPath(['m', `${purpose}'`, `${coinType}'`, `${account}'`, 1].join('/'))
          testIndex -= 4
          await testPath([purpose + HARDENED, coinType + HARDENED, account + HARDENED, 0])
          await testPath([purpose + HARDENED, coinType + HARDENED, account + HARDENED, 1])
        }
      }
    })
  })
})

network = publicFixures.network

describe('Testing HD Public Key', function () {
  publicFixures.string.forEach(test => {
    const base58Key = test[0]
    const publicKey = {
      publicKey: test[1],
      version: test[2],
      depth: test[3],
      parentFingerPrint: test[4],
      childNumber: test[5],
      chainCode: test[6]
    }

    it(`Extended key from string: ${test[0]}`, function () {
      const resultedXkey = Extended.fromString(base58Key, network)
      assert.deepEqual(resultedXkey, publicKey)
    })

    it(`Extended key to string: ${test[1]}`, function () {
      const resultedBase58Key = Extended.toString(publicKey, network)
      assert.equal(resultedBase58Key, base58Key)
    })
  })

  publicFixures.fromIndex.forEach(test => {
    it(`Deriving key ${test[0]} for index ${test[2]}`, async function () {
      const publicKey = test[0]
      const chainCode = test[1]
      const index = test[2]
      const expectedChildKey = {
        publicKey: test[3],
        chainCode: test[4],
        childNumber: test[5]
      }
      const xKey = Extended.fromExtended({ publicKey, chainCode })
      const childKey = await Extended.fromIndex(xKey, index)
      assert.deepEqual({
        publicKey: childKey.publicKey,
        chainCode: childKey.chainCode,
        childNumber: childKey.childNumber
      }, expectedChildKey)
    })
  })
})
